body {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
  font-size: 1.2rem;
  line-height: 1.5;
  color: black;
  background-image: url(../images/bg.jpg);
  background-repeat: repeat; }
  body h2,
  body button,
  body footer {
    font-weight: 400;
    color: white;
    background-color: #1a71a9; }

#wrapper {
  max-width: 1050px;
  margin: 0 auto; }

header {
  padding: 0 1rem 0 1rem; }
  header .top-area {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
    header .top-area .toggle {
      font-size: 1.7rem;
      color: #005991; }
  header #slide-menu {
    display: block;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%; }
    header #slide-menu .search-bar {
      position: relative; }
    header #slide-menu .search-bar input {
      background-color: #d8d8d8;
      border: solid 1px #606060;
      height: 35px; }
    header #slide-menu .search-icon {
      position: absolute;
      right: 2%;
      bottom: 15%; }
    header #slide-menu .search-bar, header #slide-menu .search-bar input, header #slide-menu nav, header #slide-menu .social-media {
      width: 100%; }
  header .nav-menu {
    list-style: none;
    padding: 0;
    margin: 0; }
    header .nav-menu li {
      width: 100%;
      text-align: center;
      background-color: #17649d;
      border-top: solid 1px #024d87;
      border-bottom: solid 1px #024d87; }
      header .nav-menu li a {
        color: #e7f5f9;
        text-decoration: none;
        font-size: 1.4rem; }
  header .social-media {
    font-size: 1.3rem;
    color: #1a71a9; }
    header .social-media ul {
      list-style: none;
      padding: 0;
      margin: 0;
      text-align: center;
      background-color: #f8f8f8; }
      header .social-media ul li {
        padding: 0.5rem;
        display: inline-block; }
        header .social-media ul li a {
          color: #1c6396; }

#banner {
  padding: 0px 15px 0px 15px; }
  #banner figure {
    margin: 0;
    position: relative; }
  #banner picture {
    position: relative;
    overflow: hidden; }
  #banner img {
    width: 100%; }
  #banner figcaption {
    background-color: black;
    text-transform: uppercase;
    opacity: 75%;
    color: white;
    position: absolute;
    z-index: 3;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 8px;
    padding: 8px; }

#content {
  padding: 0px 15px 0px 15px; }
  #content article {
    background-color: white;
    padding: 0px 15px 0px 15px;
    margin-bottom: .5rem;
    position: relative; }
    #content article h2 {
      background-color: white;
      color: #1a71a9;
      text-transform: uppercase;
      padding: 10px 0 0 0;
      line-height: 2rem;
      margin: 0; }
    #content article p {
      margin-top: 0;
      font-size: 1rem; }
    #content article button {
      text-transform: uppercase;
      border: none;
      font-size: 15px;
      padding: .5rem;
      position: absolute;
      right: 6px;
      bottom: 8px; }
  #content .extra {
    padding-bottom: 3rem; }
  #content div {
    margin: 2rem 0; }
